.footer-text {
  text-align: center;
  font-weight: bold;
  font-family: "Google Sans Regular";
  font-size: 16px;
}

.footer-div {
  margin-top: 2rem;
  padding: 2rem 0;
}

/* ============================================
   GLOWING BEATING HEART ANIMATION
   ============================================ */

.footer-heart {
  display: inline-block;
  margin: 0 4px;
  font-size: 20px;
  animation: heartBeat 1.5s ease-in-out infinite;
  filter: drop-shadow(0 0 8px rgba(239, 68, 68, 0.6));
  transition: transform 0.2s ease;
}

/* Heart Beat Animation */
@keyframes heartBeat {
  0%,
  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 8px rgba(239, 68, 68, 0.6));
  }
  10% {
    transform: scale(1.15);
    filter: drop-shadow(0 0 16px rgba(239, 68, 68, 0.9));
  }
  20% {
    transform: scale(1);
    filter: drop-shadow(0 0 8px rgba(239, 68, 68, 0.6));
  }
  30% {
    transform: scale(1.15);
    filter: drop-shadow(0 0 16px rgba(239, 68, 68, 0.9));
  }
  40%,
  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 8px rgba(239, 68, 68, 0.6));
  }
}

/* Pulse Glow Effect */
.footer-heart::before {
  content: "â¤ï¸";
  position: absolute;
  animation: heartGlow 1.5s ease-in-out infinite;
  filter: blur(10px);
  opacity: 0;
}

@keyframes heartGlow {
  0%,
  100% {
    opacity: 0;
    transform: scale(1);
  }
  50% {
    opacity: 0.6;
    transform: scale(1.3);
  }
}

/* Hover Effect */
.footer-heart:hover {
  transform: scale(1.3) rotate(10deg);
  filter: drop-shadow(0 0 20px rgba(239, 68, 68, 1));
  animation: heartBeatFast 0.5s ease-in-out infinite;
}

@keyframes heartBeatFast {
  0%,
  100% {
    transform: scale(1.3) rotate(10deg);
  }
  50% {
    transform: scale(1.4) rotate(10deg);
  }
}

.footer-heart-wrapper::after {
  content: "âœ¨";
  position: absolute;
  top: -10px;
  right: -10px;
  font-size: 12px;
  animation: sparkle 2s ease-in-out infinite;
  opacity: 0;
}

@keyframes sparkle {
  0%,
  100% {
    opacity: 0;
    transform: translateY(0) scale(0.5);
  }
  50% {
    opacity: 1;
    transform: translateY(-5px) scale(1);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .footer-text {
    font-size: 14px;
  }

  .footer-heart {
    font-size: 18px;
  }
}
.heart-particle {
  position: fixed;
  font-size: 16px;
  pointer-events: none;
  z-index: 9999;
  animation: particleBurst 1s ease-out forwards;
}

@keyframes particleBurst {
  0% {
    opacity: 1;
    transform: translate(0, 0) scale(1);
  }
  100% {
    opacity: 0;
    transform: translate(
        calc(var(--random-x, 0) * 100px),
        calc(var(--random-y, -100) * 1px)
      )
      scale(0);
  }
}

.footer-heart {
  display: inline-block;
  margin: 0 4px;
  font-size: 20px;
  animation: heartMonitor 1.2s ease-in-out infinite;
  filter: drop-shadow(0 0 10px rgba(239, 68, 68, 0.6));
}

@keyframes heartMonitor {
  0%,
  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 8px rgba(239, 68, 68, 0.6));
  }
  5% {
    transform: scale(1.2);
    filter: drop-shadow(0 0 20px rgba(239, 68, 68, 1));
  }
  15% {
    transform: scale(0.95);
    filter: drop-shadow(0 0 6px rgba(239, 68, 68, 0.5));
  }
  25% {
    transform: scale(1.15);
    filter: drop-shadow(0 0 18px rgba(239, 68, 68, 0.9));
  }
  35%,
  100% {
    transform: scale(1);
    filter: drop-shadow(0 0 8px rgba(239, 68, 68, 0.6));
  }
}
.footer-heart {
  display: inline-block;
  margin: 0 4px;
  font-size: 20px;
  animation: heartBeat 1.5s ease-in-out infinite;
  filter: drop-shadow(0 0 10px rgba(239, 68, 68, 0.7));
  position: relative;
  z-index: 1;
}

@keyframes heartBeat {
  0%,
  100% {
    transform: scale(1);
  }
  10%,
  30% {
    transform: scale(1.15);
  }
  20%,
  40% {
    transform: scale(1);
  }
}

/* Floating Hearts */
.footer-heart-wrapper {
  position: relative;
  display: inline-block;
}

.footer-heart-wrapper::before,
.footer-heart-wrapper::after {
  content: "â¤ï¸";
  position: absolute;
  font-size: 10px;
  opacity: 0;
  pointer-events: none;
}

.footer-heart-wrapper::before {
  left: -20px;
  animation: floatHeart 3s ease-in-out infinite;
}

.footer-heart-wrapper::after {
  right: -20px;
  animation: floatHeart 3s ease-in-out infinite 1.5s;
}

@keyframes floatHeart {
  0% {
    opacity: 0;
    transform: translateY(0) scale(0.5);
  }
  50% {
    opacity: 0.6;
  }
  100% {
    opacity: 0;
    transform: translateY(-40px) scale(1);
  }
}
